import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { FaFileDownload } from "react-icons/fa";

const AppointmentsLayer = () => {
  const [userBooking, setUserBooking] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [viewBooking, setViewBooking] = useState(null);
  const [cancellingId, setCancellingId] = useState(null);

  const [cancelModal, setCancelModal] = useState(false);
const [selectedBookingId, setSelectedBookingId] = useState(null);
 const [confirmModal,setConfirmModal] = useState(false);

  useEffect(() => {
    fetchUserBookings();
  }, []);

  const fetchUserBookings = () => {
    const userId = localStorage.getItem("userId");

    if (!userId) {
      console.error("No userId found in localStorage");
      return;
    }

    axios
      .get(`${process.env.REACT_APP_BACKEND_URL}/api/bookings/user/${userId}`)
      .then((response) => {
          console.log("User bookings response:", response.data);
        setUserBooking(response.data.bookings);
      })
      
      
      .catch((error) => {
        console.error("Error fetching bookings:", error);
        toast.error("Failed to load bookings");
      });
  };

  const handleViewDetails = (service) => {
    setViewBooking(service);
    setShowModal(true);
  };

 const handleCancelBooking = async () => {
  if (!selectedBookingId) return;

  try {
    setCancellingId(selectedBookingId);

    await axios.patch(
      `${process.env.REACT_APP_BACKEND_URL}/api/bookings/${selectedBookingId}/status`,
      { status: "Cancelled" }
    );

    toast.success("Booking cancelled successfully");
    setTimeout(()=>{
      fetchUserBookings()
    },3000)
  } catch (error) {
    console.error("Error cancelling booking:", error);
    toast.error(error.response?.data?.message || "Failed to cancel booking");
  } finally {
    setCancellingId(null);
    setSelectedBookingId(null);
    setCancelModal(false);
  }
};

  const handleDownload = (bookingId) => {
    window.open(
      `${process.env.REACT_APP_BACKEND_URL}/api/invoice/${bookingId}`,
      "_blank"
    );
  };

  // pagination for appointments
  const [currentPage, setCurrentPage] = useState(1);
  const bookingPerPage = 7; // Changed to 10 records per page
  const indexOfLastBooking = currentPage * bookingPerPage;
  const indexOfFirstBooking = indexOfLastBooking - bookingPerPage;
  const currentBookings = userBooking.slice(
    indexOfFirstBooking,
    indexOfLastBooking
  );
  const totalPages = Math.ceil(userBooking.length / bookingPerPage);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

       const handlePageChangeWithScroll = (page) => {
  handlePageChange(page); // your existing page change logic
  window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
};

  const renderPagination = () => {
    const pages = [];
    const maxVisible = 4; // ðŸ‘ˆ only show 4 pages at a time
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalPages) {
      endPage = totalPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

       pages.push(
    <li
      key="first"
      className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
       
      >
        First
      </button>
    </li>
  );

    // Prev Button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)}
        >
          Prev
        </button>
      </li>
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button className="page-link" onClick={() => handlePageChangeWithScroll(i)}>
            {i}
          </button>
        </li>
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalPages && handlePageChangeWithScroll(currentPage + 1)
          }
        >
          Next
        </button>
      </li>
    );

      pages.push(
      <li
        key="last"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalPages)}
        >
          Last
        </button>
      </li>
    );

    return pages;
  };

   const modalRef = useRef(null);
    
        useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
              showModal
            ) {
              showModal(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // ðŸ”‘ Scroll lock logic
          if (showModal) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [showModal]);

        useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
              cancelModal
            ) {
              cancelModal(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // ðŸ”‘ Scroll lock logic
          if (cancelModal) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [cancelModal]);

        const handleCancelCheck = async (service) => {
          console.log("Calling service",service);
          
  if (service?.status !== "pending") return;

  try {
    setCancellingId(service.bookingId);

    const response = await axios.get(
      `${process.env.REACT_APP_API_URL}/check/${service.bookingId}`
    );

    // âœ… If response is 200 means uploads present â†’ block cancel
    if (response.status === 200) {
      alert("Centre already uploaded documents â€” you cannot cancel this booking.");
      setCancellingId(null);
      return;
    }

  } catch (error) {
    // âœ… If server says uploads required â†’ allow cancel
    if (error.response?.status === 400) {
      setSelectedBookingId(service.bookingId);
      setCancelModal(true); // âœ… Open confirmation modal
    } else {
      alert("Error verifying report status!");
      console.error(error);
    }
  } finally {
    setCancellingId(null);
  }
};

        useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
              setConfirmModal
            ) {
              setConfirmModal(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // ðŸ”‘ Scroll lock logic
          if (confirmModal) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [confirmModal]);


const shouldDisableCancelButton = (item) => {
  if (item.status.toLowerCase() !== "pending") return true;
  if (!item.appointmentDate || !item.appointmentTime) return true;

  const datePart = item.appointmentDate;
  const startTime = item.appointmentTime.split("-")[0].trim();

  const timeMatch = startTime.match(/(\d+):(\d+)\s*(am|pm)/i);
  if (!timeMatch) return true;

  let [_, hh, mm, period] = timeMatch;
  hh = parseInt(hh, 10);
  mm = parseInt(mm, 10);

  if (period.toLowerCase() === "pm" && hh !== 12) hh += 12;
  if (period.toLowerCase() === "am" && hh === 12) hh = 0;

  const appointmentDateTime = new Date(`${datePart}T${String(hh).padStart(2, "0")}:${String(mm).padStart(2, "0")}:00`);
  const now = new Date();
  
  // Calculate time difference in hours
  const timeDifferenceHours = (appointmentDateTime.getTime() - now.getTime()) / (1000 * 60 * 60);
  
  // Disable cancel button if appointment is in the past OR within 2 hours from now
  return timeDifferenceHours <= 2;
};

  return (
    <div className="dashboard-body__content">
      <ToastContainer position="top-right" autoClose={3000} />

      {/* ========================= Statement section start =========================== */}
      <div className="row gy-4">
        <div className="col-12">
          <div className="card common-card border border-gray-five">
            <div className="card-body">
              <h4>Booking Statement</h4>
              <div className="table-responsive">
                <table className="table text-body mt--24">
                  <thead>
                    <tr>
                      <th>Booking Id</th>
                      <th>Date</th>
                      <th>Time Slots</th>
                      <th>Patient Name</th>
                      <th>Centre Name</th>
                      {/* <th>Centre Email</th> */}
                      <th>Centre Mobile</th>
                      <th>Service Type</th>
                      <th>Service Name</th>
                      <th>Doctor Name</th>
                      <th>Price</th>
                      <th>Status</th>
                      <th>Booking Summary</th>
                      <th>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentBookings.length> 0 ? 
                    (currentBookings.map((booking, idx) =>
                      booking.services.map((service, i) => (
                        <tr key={`${idx}-${i}`}>
                          <td data-label="BookingId">{service?.bookingId}</td>
                       
                          <td data-label="Date">{service?.appointmentDate}</td>
                          <td data-label="Time">{service?.appointmentTime}</td>
                             <td data-label="Patient Name">{booking?.user?.firstName}</td>
                          <td data-label="Patient Name">
                            {service?.centre?.name}
                          </td>
                          {/* <td data-label="Patient Email">
                            {service?.centre?.email}
                          </td> */}
                          <td data-label="Patient Mobile">
                            {service?.centre?.phoneNumber}
                          </td>
                          <td data-label="Service Type">{service?.type}</td>
                          <td data-label="Category">{service?.name}</td>
                          <td data-label="Category">{service?.doctors}</td>

                          <td data-label="Price">â‚¹{service?.totalPrice}</td>
                          <td data-label="Status">
                            <span
                              className={`badge ${
                                service?.status === "Confirmed"
                                  ? "bg-success"
                                  : service?.status === "Cancelled"
                                  ? "bg-danger"
                                  : service?.status === "Completed"
                                  ? "bg-success"
                                  : "bg-warning"
                              }`}
                            >
                                                       {service?.status==="pending" ?"Upcoming":service?.status}

                            </span>
                          </td>
                          <td>
                            <FaFileDownload
                              className="cursor-pointer text-xl"
                              onClick={() => handleDownload(service?.bookingId)}
                            />
                            
                          </td>


                          <td
                            data-label="Actions"
                            style={{ display: "flex", gap: "5px" }}
                          >
                            <Link
                              className="btn btn-sm btn-primary"
                              onClick={() => handleViewDetails(service)}
                            >
                              {/* <i className="far fa-eye" /> */}
                              View
                            </Link>
                            <button
  className={`btn btn-sm ${
    service?.status === "pending"
      ? "btn-danger"
      : "btn-secondary"
  }`}
  onClick={async () => {
    if (service?.status === "pending") {
           if (shouldDisableCancelButton(service)) {
          setConfirmModal(true);
          return;
        }
        
      try {
        // Check if report exists before showing cancel modal
        const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/reports/check/${service.bookingId}`
);
        
        if (response.status === 200) {
          // Report already exists, show alert
          toast.error("Centre already uploaded document. You cannot cancel this booking.");
        } else {
          // No report exists, show cancel confirmation modal
          setSelectedBookingId(service.bookingId);
          setCancelModal(true);
        }
      } catch (error) {
        console.error("Error checking report:", error);
        // If there's an error, proceed with cancel modal as fallback
        setSelectedBookingId(service.bookingId);
        setCancelModal(true);
      }
    }
  }}


   disabled={
      service?.status !== "pending" ||
      cancellingId === service.bookingId 
    }

  title={
    service?.status !== "pending"
      ? `Cannot cancel ${service?.status} booking`
      : "Cancel booking"
  }
  style={{
    cursor:
      service?.status === "pending" &&
      cancellingId !== service.bookingId
        ? "pointer"
        : "not-allowed",
  }}
>
  {cancellingId === service.bookingId ? (
    <span
      className="spinner-border spinner-border-sm"
      role="status"
      aria-hidden="true"
    ></span>
  ) : (
    "Cancel"
  )}
</button>
                          </td>
                        </tr>
                      ))
                    )) :
                    (<tr>
      <td colSpan="12" className="text-center text-muted py-4">
        No bookings have been done yet.
      </td>
    </tr>)}
                  </tbody>
                </table>

      
              </div>
              {userBooking.length>0 && (   <div className="flx-between gap-2">
                  <div className="paginate-content flx-align flex-nowrap gap-3">
                    <span className="paginate-content__text fs-14">
                      Showing {indexOfFirstBooking + 1} â€“{" "}
                      {Math.min(indexOfLastBooking, userBooking.length)} of{" "}
                      {userBooking.length}
                    </span>
                  </div>
                  <nav aria-label="Doctor pagination" className="mt-3">
                    <ul className="pagination justify-content-center">
                      {renderPagination()}
                    </ul>
                  </nav>
                </div>)}
                  
            </div>
          </div>
        </div>
      </div>
      {/* ========================= Statement section End =========================== */}

{cancelModal && (
  <div
    style={{
      position: "fixed",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%",
      backgroundColor: "rgba(0,0,0,0.4)",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      zIndex: 2000,
    }}
    onClick={() => setCancelModal(false)}
  >
    <div
      style={{
        backgroundColor: "#fff",
        padding: "25px",
        borderRadius: "10px",
        width: "400px",
        maxWidth: "90%",
        boxShadow: "0px 0px 15px rgba(0,0,0,0.3)",
        textAlign: "center",
      }}
      onClick={(e) => e.stopPropagation()}
    >
      <h5 style={{ fontWeight: "bold", marginBottom: "10px" }}>
        Cancel Booking?
      </h5>
      <p style={{ fontSize: "14px", color: "#555" }}>
        Are you sure you want to cancel this booking?
      </p>

      <div style={{ display: "flex", gap: "10px", justifyContent: "center" }}>
        <button
          className="btn btn-success"
          disabled={cancellingId === selectedBookingId}
          onClick={handleCancelBooking}
        >
          {cancellingId === selectedBookingId ? "Processing..." : "Yes"}
        </button>
        <button
          className="btn btn-danger"
          disabled={cancellingId === selectedBookingId}
          onClick={() => setCancelModal(false)}
        >
          No
        </button>
      </div>
    </div>
  </div>
)}

{confirmModal && (
  <div
    className="modal-backdrop"
    style={{
      position: "fixed",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%",
      backgroundColor: "rgba(0,0,0,0.5)",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      zIndex: 9999,
    }}
    onClick={(e) => {
      if (e.target.className === 'modal-backdrop') {
        setConfirmModal(false);
      }
    }}
  >
    <div
      className="modal-content"
      style={{
        backgroundColor: "white",
        padding: "25px",
        borderRadius: "10px",
        width: "450px",
        maxWidth: "90%",
        textAlign: "center",
      }}
      onClick={(e) => e.stopPropagation()}
    >
      {/* Warning Icon */}
      <div style={{
        width: "60px",
        height: "60px",
        borderRadius: "50%",
        backgroundColor: "#fff3cd",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        margin: "0 auto 15px"
      }}>
        <i className="fas fa-exclamation-triangle" style={{ fontSize: "24px", color: "#856404" }}></i>
      </div>
      
      <h5 style={{ marginBottom: "10px", color: "#333" }}>
        Cancellation Restricted
      </h5>
      
      <p style={{ 
        color: "#666", 
        marginBottom: "20px",
        fontSize: "15px"
      }}>
        This booking cannot be cancelled as it's within 2 hours of the scheduled appointment.
      </p>
      
      <div style={{ display: "flex", justifyContent: "center" }}>
        <button
          className="btn btn-primary"
          onClick={() => setConfirmModal(false)}
          style={{
            padding: "8px 25px",
            fontSize: "14px"
          }}
        >
          I Understand
        </button>
      </div>
    </div>
  </div>
)}

     {showModal && viewBooking && (
  <div
    style={{
      position: "fixed",
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: "rgba(0,0,0,0.5)",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      zIndex: 1000,
      padding: "10px", // âœ… adds spacing on small screens
      overflowY: "auto", // âœ… allows scrolling if content too big
    }}
    onClick={() => setShowModal(false)}
  >
    <div
      style={{
        backgroundColor: "#fff",
        borderRadius: "10px",
        maxWidth: "700px",
        width: "100%",
        boxShadow: "0 0 10px rgba(0,0,0,0.3)",
        position: "relative",
        overflow: "hidden",
        maxHeight: "90vh", // âœ… limits modal height to viewport height
        overflowY: "auto", // âœ… scroll inside the modal if content exceeds
      }}
      onClick={(e) => e.stopPropagation()}
    >
      <div
        style={{
          backgroundColor: "#3a3a3a",
          color: "#fff",
          padding: "15px 20px",
          fontSize: "18px",
          fontWeight: "bold",
          position: "sticky", // âœ… header stays visible when scrolling
          top: 0,
          zIndex: 10,
        }}
      >
        Booking Details
        <button
          onClick={() => setShowModal(false)}
          style={{
            position: "absolute",
            top: "10px",
            right: "10px",
            background: "transparent",
            border: "none",
            fontSize: "22px",
            color: "#fff",
            cursor: "pointer",
          }}
        >
          &times;
        </button>
      </div>

      <div
        style={{
          display: "flex",
          padding: "20px",
          flexWrap: "wrap",
          gap: "20px",
        }}
      >
        {/* Left Column */}
        <div style={{ flex: "1", minWidth: "250px" }}>
          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Booking ID</strong>
            </p>
            <p>{viewBooking?.bookingId}</p>
          </div>

          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Appointment Date</strong>
            </p>
            <p>{viewBooking?.appointmentDate}</p>
          </div>

          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Appointment Time</strong>
            </p>
            <p>{viewBooking?.appointmentTime}</p>
          </div>

          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Centre Name</strong>
            </p>
            <p>{viewBooking.centre?.name}</p>
          </div>

          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Centre Email</strong>
            </p>
            <p>{viewBooking.centre?.email}</p>
          </div>

          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Centre Mobile</strong>
            </p>
            <p>{viewBooking?.centre?.phoneNumber}</p>
          </div>
        </div>

        {/* Right Column */}
        <div style={{ flex: "1", minWidth: "250px" }}>
          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Booking Type</strong>
            </p>
            <p>{viewBooking.type}</p>
          </div>

          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>
                {viewBooking?.type === "package"
                  ? "Package Name"
                  : "Service Name"}
              </strong>
            </p>
            <p>{viewBooking?.name}</p>
          </div>

          <div style={{ marginBottom: "20px" }}>
            {viewBooking?.type === "package" ? (
              <p>
                <strong style={{ color: "black" }}>Services Name</strong>
              </p>
            ) : (
              <p>
                <strong style={{ color: "black" }}>Category Name</strong>
              </p>
            )}

            {viewBooking?.type === "package" ? (
              viewBooking?.includedServices?.map((service, key) => (
                <p key={key}>{service}</p>
              ))
            ) : (
              <p>{viewBooking?.category}</p>
            )}
          </div>

          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Doctor Name</strong>
            </p>
            {viewBooking?.doctors?.map((doc, index) => (
              <p key={index}>{doc}</p>
            ))}
          </div>

          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Price</strong>
            </p>
            <p>â‚¹{viewBooking?.totalPrice}</p>
          </div>

          <div style={{ marginBottom: "20px" }}>
            <p>
              <strong style={{ color: "black" }}>Status</strong>
            </p>
            <p>{viewBooking?.status==="pending"?"Upcoming":viewBooking?.status}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
)}

    </div>
  );
};

export default AppointmentsLayer;
